.inputBorder{
  width: 90%;
  margin: 0 auto;
}

.result {
  display: block;
  margin: 0 auto;
  margin-top: 12.5px;
  border-radius: 9px;
  padding: 15px;
  max-width: 450px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  background-color: white;
  cursor: not-allowed;
  text-align: center;
  min-height: 34px;
}

.animation {
  max-width: 480px;
  width: 95%;
  height: 4px;
  background-color: rgba(163, 23, 23, 0.866);
  margin: 0 auto;
  margin-top: 12.5px;
  visibility: hidden;
}